@import '../../assets/scss/app.scss';


.app {
  position: relative;
  background: #08151F;
  //padding-top: 50px;
  min-height: 100vh;
  box-sizing: border-box;

  @include desktop() {
    //padding-top: 82px;
  }

  .stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .notAnimateObjects {
    overflow: hidden;
    transition: 0.3s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .left_na_1 {
      @include onlyDesktop() {
        position: absolute;
        top: 612px;
        left: calc(50% - 955px);
      }
    }
    .right_na_1 {
      @include onlyDesktop() {
        position: absolute;
        top: 1180px;
        right: 0;
      }
    }
    .right_na_2 {
      @include onlyDesktop() {
        position: absolute;
        top: 2140px;
        right: 0;
      }
    }
    .left_na_2 {
      @include onlyDesktop() {
        position: absolute;
        top: 3615px;
        left: 0;
      }
    }
    .right_na_3 {
      @include onlyDesktop() {
        position: absolute;
        bottom: 580px;
        right: 200px;
      }
    }
    .left_na_3 {
      @include onlyDesktop() {
        position: absolute;
        bottom: -80px;
        left: 180px;
      }
    }

  }

  .animateObjects {
    overflow: hidden;
    transition: 0.3s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .left_1 {
      @include onlyDesktop() {
        position: absolute;
        width: 193px;
        height: 173px;
        top: 1435px;
        left: calc(50% - 670px);
      }
    }

    .right_1 {
      @include onlyDesktop() {
        position: absolute;
        width: 170px;
        height: 165px;
        top: 885px;
        left: calc(50% + 450px);
      }
    }

    .right_2 {
      @include onlyDesktop() {
        position: absolute;
        width: 319px;
        height: 282px;
        top: 3270px;
        left: calc(50% + 400px);
      }
    }

    .left_2 {
      @include onlyDesktop() {
        position: absolute;
        width: 225px;
        height: 178px;
        top: 3950px;
        left: calc(50% - 725px);
      }
    }

    .right_3 {
      @include onlyDesktop() {
        position: absolute;
        width: 149px;
        height: 139px;
        top: 5440px;
        left: calc(50% + 500px);
      }
    }

    .left_3 {
      @include onlyDesktop() {
        position: absolute;
        width: 210px;
        height: 176px;
        top: 5805px;
        left: calc(50% - 715px);
      }
    }


  }



  .burgerButton {
    border: 0;
    background: none;
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 9999;

    @include desktop() {
      display: none;
    }

    svg {
      width: 22px;
      height: 22px;
    }

  }


}